import mongoose from 'mongoose';
let Schema = mongoose.Schema;

let DataSchema = new Schema({

    roomName: {
        type: String,
        required: true
    },
    hub: [{
        type: Schema.ObjectId,
        ref: 'Hub',
        hubName: Hub.hubName
    }],
    teachers: [{
        type: Schema.ObjectId,
        ref: 'Teacher'
    }],
    students: [{
        type: Schema.ObjectId,
        ref: 'Student'
    }]
});

module.exports = mongoose.model('Data', DataSchema);